<template>
	<view class="player-audio" v-if="playListId !== 0">
		<view class="player" v-if="songsData">
			<view class="player-img">
				<image :src="songsData[this.playIndex].al.picUrl+'?param=84y84'" class="img-w" :class="{'play-rotate':play}"></image>
			</view>
			<view class="player-title">
				<text class="title">{{songsData[this.playIndex].name}}</text>
			</view>
			<view class="playSuspend" >
				<image src="@/static/zanting.png" class="iocnSuspendPlay" v-if="play"></image>
				<image src="@/static/icon_play.png" class="iocnSuspendPlay" v-else></image>
				<canvas  canvas-id="firstCanvas" id="firstCanvas" class="SongsCanvas" @tap="onPlay"></canvas>
			</view>
			<view class="player-list iconfont icon-width-56 icon-icon"></view>
			
		</view>
	</view>
</template>

<script>
	import { uniAudio, audioPlay } from '@/common/player.js';
	import app from "@/App.vue";
	var _self;
	var canvaColumn=null;
	var canplayTime = null;
	export default{
		data(){
			return{
				play:false,
				ctx:null,
				waitFlag:false,
				nextIndex:1,//下一首
				prevIndex:0,//上一首
				audioDuration:0,
				cxt:null,
				audioArc:23,
				playlistData:null,
				songsData:null,
				songsId:0,
			}
		},
		props:{
			playListId:{
				type:Number,
				default:0
			},
			playIndex:{
				type:Number,
				default:0
			}
		},
		created() {
			console.log(this.playListId)
			console.log(this.playIndex)
			let _this = this;
			uni.getSystemInfo({
				success:(res)=>{
					console.log(res.windowWidth)
					this.audioArc= (res.windowWidth/750)*84/2;
				}
			})
			this.cxt = uni.createCanvasContext('firstCanvas',this);
			this.getPlaylist(this.playListId)
			/*if(!uniAudio.paused){
				this.onCanplay();
				this.onTimeUpdate();
				this.onWaiting();
				this.onEnded();
			}else{
				this.play=false;
				let audioDuration; 
				uni.getStorage({
				    key: 'audioDuration',
				    success: function (res) {
						console.log(res)
				       audioDuration=res.data;
					   let currentTime = 0;
					   uni.getStorage({
					       key: 'currentTime',
					       success: function (res) {
					   		console.log(res)
					        currentTime=res.data;
					   	   let timeNum = audioDuration? currentTime/audioDuration*2:0;
					   	   console.log(timeNum)
					   	   _this.dynamic(timeNum);
					       }
					   });
				    }
				});
				
				
				console.log('暂停之后的时间')
			}*/
			
			 // this.timeCanvas(0);
		},
		beforeUpdate() {
			
		},
		updated() {
			
		},
		
		methods:{
			
			onPlay(){
				console.log(this.play)
				if(this.play){
					//暂停
					uniAudio.pause();
					uniAudio.onPause(()=>{
						this.play = false;
						uni.setStorage({
							key:'currentTime',
							data:uniAudio.currentTime
						})
					})
				}else{
					uni.getStorage({
						key:'currentTime',
						success:(res)=>{
							console.log(res)
							if(!res.data || res.data === 0){
								//第一次打开页面时没有播放器
								if(!uniAudio.src  ){
									this.onGetSongs(this.songsId);
									return false;
								}else{
									uniAudio.onError((res) => {
										this.onGetSongs();
										console.log(res);
										uniAudio.parse();
										uniAudio.onParse(()=>{
											this.play = false;
										})
									});
								}
							}else{
								this.onGetSongs(this.songsId);
								uniAudio.seek(res.data);
							}
							
							//播放
							uniAudio.play();
							uniAudio.onPlay(()=>{
								this.play = true;
							})
							this.onCanplay();
							this.onTimeUpdate();
							this.onWaiting();
							this.onEnded();
						},
						fail:(res)=>{
							//第一次打开软件
							this.onGetSongs(this.songsId);
						}
					})
					
				}
			},
			onGetSongs(id){
				this.$http.get(this.$_musicUrl,{params:{id:id}}).then(res=>{
					clearTimeout(canplayTime);
					audioPlay(res.data[0].url,(play)=>{
						console.log(play)
						this.play = play;
						uniAudio.play();
					})
					this.onWaiting();
					this.onEnded();
					
				})
			},
			//音频自然播放结束事件
			onEnded(){
				uniAudio.onEnded(()=>{
					this.play = false;
					uni.setStorage({
						key:'currentTime',
						data:0
					})
				})
			},
			//音频播放进度更新事件
			onTimeUpdate(){
				console.log('音频播放进度更新事件')
				uniAudio.onTimeUpdate(()=>{
					let timeNum = uniAudio.currentTime/this.audioDuration*2;
					this.dynamic(timeNum);
					this.play = true;
				})
			},
			
			//绘制时间进度
			dynamic(radian){
					// uni.$emit("timeCanvasPlay",radian)
					this.timeCanvas(radian);
			},
			//绘画
			timeCanvas(num){ 
				if(this.cxt) {
					this.cxt.setLineWidth(2);
					this.cxt.setStrokeStyle("#333333");
					this.cxt.arc(this.audioArc,this.audioArc,this.audioArc-1,0,num* Math.PI, false);
					this.cxt.stroke();
					this.cxt.draw();
				};
				
				console.log(this.cxt)
				console.log(num)
			},
			canvasIdErrorCallback: function (e) {
			    console.error(e.detail.errMsg)
			},
			//网络事件
			onWaiting(){
				uniAudio.onWaiting(() => {
					console.log('等待网络的时候音频暂停')
				  uniAudio.pause() // 等待网络的时候音频暂停
					this.waitFlag=true;
					this.play=false;
				})
				console.log(this.waitFlag)
				// 音频准备就绪的回调
				uniAudio.onCanplay(() => {
				  if (this.waitFlag) { // 如果是onWaiting触发的暂停，就立即播放
				  console.log('play()方法看上去能重新触发onTimeUpdate()')
					  this.onCanplay();
				      uniAudio.play() // play()方法看上去能重新触发onTimeUpdate()回调
					  this.waitFlag=false;// 取消相应的flag标志位
					  this. play=true;
					  this.onTimeUpdate();
				  }
				})
			},
			//可播放状态
			onCanplay(){
				console.log(uniAudio.duration)
				canplayTime = setTimeout(() => {
					if(uniAudio.duration*1>0||uniAudio.buffered*1>0) {
						// 部分iphone手机audioContext.duration 会为为0，所以判断audioContext.duration是否为0
							this.audioDuration = uniAudio.duration==0?uniAudio.buffered:uniAudio.duration;
							uni.setStorage({
								key:'audioDuration',
								data:this.audioDuration,
								success:function(res){
									console.log(res)
								}
							})

						}
				}, 1000)
			},
			
			//清除监听
			
			//获取歌曲详情
			getsongDetail(ids){
				this.$http.get(this.$_songDetail,{params:{ids:ids}}).then(res=>{
					console.log(res)
					this.songsData = res.songs;
					this.songsId =  res.songs[this.playIndex].id;
				})
			},
			//请求歌单
			getPlaylist(id){
				this.$http.get(this.$_playlistDetail,{params:{id:id}}).then(res=>{
					console.log(res)
					this.songsData
					this.getsongDetail(this.songIds(res.privileges));
				})
			},
			songIds(arr){
				let ids = '';
				arr.map((item,index)=>{
					ids = index <arr.length-1? ids + item.id + ',':ids + item.id;
				})
				return ids;
			},
		}
	}
</script>

