		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		padding: 30rpx;
		border-top: 2rpx solid #e6e6e6;
		width: 100%;
		background-color: #FFFFFF;
		box-sizing: border-box;
	}
