		width: 127rpx;
		height: 127rpx;
		background-image: url(data:image/png;base64,);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 127rpx 127rpx;
		position: absolute;
		left: 30rpx;
		top: -10rpx;
		flex-shrink:0;
		display: flex;
		align-items: center;
		justify-content: center;
	}
