		width: 76rpx;
		height: 76rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink:0;
		position: relative;
	}
	.iocnSuspendPlay{
		width: 30rpx;
		height: 30rpx;
	}
	.player-title{
		width: 300rpx;
		padding-left: 150rpx;
